* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html, body {
	margin: 0;
	padding: 0;
	min-height: 100%;
  width: 100%;
}

body, button {
	font-family: "Maven Pro", "Helvetica Neue", Helvetica, Arial, sans-serif;
	line-height: 1.48;
}

.muted { color: "#aaa"; color: #617c8f; color: var(--color-blue-grey, "#aaa"); }

.color-ap { color: #eb2f59; color: var(--color-pink); }

.color-twitter { color: #0d7ecc; color: var(--color-blue); }

img.ext {
	max-width: 112px;
}

a.ext {
  position: relative;
  text-decoration: none;
  display: inline-block;
  color: black;
  padding: 0 1px;
  -webkit-transition: color ease 0.24s;
  transition: color ease 0.24s;
	-webkit-transform: translateZ(0px);
}

a.ext:visited {
  color: #c30c70;
  color: var(--color-purple);
}

a.ext::after {
  content: '';
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 10%;
  left: 0;
  bottom: 0;
  background: "#aaa";
  background: #ff2800;
  background: var(--link-color, "#aaa");
  -webkit-transition: all ease 0.24s;
  transition: all ease 0.24s;
}

a.ext:hover {
  color: #ccc;
  color: #eee;
  color: var(--link-color-hover, #ccc);
}

a.ext:hover::after {
  height: 92%;
}

summary {
  color: #0d7ecc;
  color: #373a3e;
  color: var(--color-dark-gray, #0d7ecc);
  padding-bottom: 2px;
  cursor: pointer;
}

summary:not(summary:focus) p {
  text-decoration: underline;
}

summary:focus {
  padding-bottom: 0px;
  outline: none;
  border-bottom: 2px dotted "#ccc";
  border-bottom: 2px dotted #617c8f;
  border-bottom: 2px dotted var(--color-blue-grey, "#ccc");
}

summary p { display: inline !important; }
